import React from 'react'

const Notification = ({ message, type }) => {
    if (message === '') {
      return null
    }
    if (type === 'error') {
        return (
          <div className={`message error`}>
          {message}
        </div>
        )
      }
      else {
  
    return (
      <div className="message">
        {message}
      </div>
    )
      }
}

export default Notification