import React, {useState, useEffect} from 'react';
import Persons from './Persons';
import PersonForm from './PersonForm';
import Filter from './Filter';
import Notification from './Notification';
import personsService from './services/persons';

const App = () => {
  const [persons, setPersons] = useState([]);
  const [newName, setNewName] = useState('');
  const [newNumber, setNewNumber] = useState('');
  const [showAll, setShowAll] = useState(true);
  const [newFilter, setNewFilter] = useState('');
  const [notification, setNotification] = useState({message: '', type: ''});

  useEffect(() => {
    personsService.getAll().then((personsData) => {
      setPersons(personsData);
    });
  }, []);
  const handleNameChange = (evt) => {
    setNewName(evt.target.value);
  };
  const handleNumberChange = (evt) => {
    setNewNumber(evt.target.value);
  };
  const setFilterValue = (evt) => {
    setShowAll(false);
    setNewFilter(evt.target.value);
  };
  const personsToShow = showAll
    ? persons
    : persons.filter(
        (person) =>
          person.name.includes(newFilter) ||
          person.name.toLowerCase().includes(newFilter.toLowerCase()),
      );

  const isDuplicate = () => persons.some((item) => item.name === newName);
  const addNewPerson = (evt) => {
    evt.preventDefault();
    if (!newName || !newNumber) return;
    const newPerson = {
      name: newName,
      number: newNumber,
    };
    if (isDuplicate()) {
      if (
        window.confirm(
          `This person is already added to phonebook. Replace the old number with a new one?`,
        )
      ) {
        const existingPerson = persons.find(
          (person) => person.name === newPerson.name,
        );
        personsService
          .update(existingPerson.id, newPerson)
          .then((data) =>
            setPersons(
              persons.map(
                (person) => (person.id !== data.id ? person : data),
                false,
              ),
            ),
          );
        setNotification({
          ...notification,
          message: `Number for ${existingPerson.name} is changed successfully`,
        });
      }
    } 
    else {
      personsService.create(newPerson).then((response) => {
        setPersons(persons.concat(response));
        setNotification({
        ...notification,
        message: `${newPerson.name} is added to phonebook`,
      });
      })
      .catch(error => {
      setNotification({
          ...notification,
          message: error.response.data.error,
          type: 'error',
        });
    });
      }
    setTimeout(() => {
      setNotification({...notification, message: '', type: ''});
    }, 5000);
    setNewName('');
    setNewNumber('');
  };
  
  const handleDelete = (id, name) => {
    const confirmDeleting = window.confirm(
    `Do you really want to delete ${name} from phonebook?`,
  );
  if (confirmDeleting) {
    personsService
      .deleteItem(id, name)
      .then((response) => {
        setPersons(persons.filter((person) => person.id !== id));
      })
      .catch((error) => {
        setNotification({
          ...notification,
          message: `${name} has already been deleted`,
          type: 'error',
        });
        setPersons(persons.filter((person) => person.name !== name));
        setTimeout(() => {
          setNotification({...notification, message: '', type: ''});
        }, 5000);
      });
      }
  };

  return (
    <>
      <h2>Phonebook</h2>
      <Filter filter={newFilter} onChangeAction={setFilterValue} />
      <h3>Add a new</h3>
      <PersonForm
        onSubmitAction={addNewPerson}
        name={newName}
        onChangeName={handleNameChange}
        number={newNumber}
        onChangeNumber={handleNumberChange}
      />
      <h3>Numbers</h3>
      <Persons persons={personsToShow} handleDelete={handleDelete} />
      <Notification message={notification.message} type={notification.type} />
    </>
  );
};

export default App;
